import React from "react";
import LazyLoad from "react-lazy-load";
import { Link } from "react-router-dom";
import Favorite from "./Favorite";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHeart } from "@fortawesome/free-regular-svg-icons";
import { faHeart as faSolidHeart } from "@fortawesome/free-solid-svg-icons";

class Recipes extends React.Component {

  handleOnClick = recipe => {
    this.props.setFavorite(recipe);

    let index = this.props.recipes.indexOf(recipe);

    this.props.setIndex(index);
  };

  displayHeart = recipe => {
    let heart;

    if (recipe.favorite) {
      heart = this.displaySolid();
    } else {
      heart = this.displayBorder();
    }

    return <div>{heart}</div>;
  };

  displaySolid = () => {
    return (
      <div>
        <FontAwesomeIcon
          icon={faSolidHeart}
          size="2x"
          style={{ marginBottom: "1rem", cursor: "pointer", color: "#FF5654" }}
        />
      </div>
    );
  };

  displayBorder = () => {
    return (
      <div>
        <FontAwesomeIcon
          icon={faHeart}
          size="2x"
          style={{ marginBottom: "1rem", cursor: "pointer", color: "#FF5654" }}
        />
      </div>
    );
  };

  render() {
    return (
      <div className="container">
        <div className="row">
          {this.props.recipes !== null &&
            this.props.recipes.slice(0, this.props.count).map(recipe => {
              return (
                <div
                  key={recipe.recipe_id}
                  className="col-md-4"
                  style={{ marginBottom: "2rem" }}
                >
                  <div className="recipes__box">
                    <LazyLoad>
                      <div className="image">
                        <img
                          className="recipes__box-img"
                          src={recipe.image_url}
                          alt={recipe.title}
                        />
                      </div>
                    </LazyLoad>
                    <div className="recipe__text">
                      <h5 className="recipes__title">
                        {recipe.title.length < 20
                          ? `${recipe.title}`
                          : `${recipe.title.substring(0, 25)}...`}
                      </h5>
                      <p className="recipes__subtitle">
                        Publisher: <span>{recipe.publisher}</span>
                      </p>
                    </div>
                    <button className="recipe_buttons">
                      <Link
                        to={{
                          pathname: `/recipe/${recipe.recipe_id}`,
                          state: { recipe: recipe.title }
                        }}
                      >
                        View Recipe
                      </Link>
                    </button>
                    <div className="heart">
                      <span onClick={() => this.handleOnClick(recipe)}>
                        {this.displayHeart(recipe)}
                      </span>
                    </div>
                  </div>
                </div>
              );
            })}
        </div>
      </div>
    );
  }
}

export default Recipes;
