import React from "react";
import LazyLoad from "react-lazy-load";
import { Link } from "react-router-dom";
import Favorite from "./Favorite";

class Favorites extends React.Component {
  state = {
    favorites: []
  };

  componentDidMount = () => {
    if (this.props.location.state) {
      const favorites = this.props.location.state.recipes;
      this.setState({ favorites });
    }
  };

  displayHeart = () => {
    const index = this.props.location.state.index;
    
    if (index > -1 && this.props.location.state.recipes[index].favorite === true) {
      return {"index":index, "display":this.props.location.state.recipes[index].favorite};
    } else {
      if (index > -1) {
        return {"index":index, "display":this.props.location.state.recipes[index].favorite};
      }
    }
  }

  render() {
    return (
      <div className="container">
        <div className="row">
          <header className="App-header">
            <div>
              <h1 className="App-title">
                <Link
                  to={{
                    pathname: `/`,
                  }}
                  style={{ textDecoration: "none" }}
                >
                  Go Home
                </Link>
              </h1>{" "}
            </div>
          </header>
          {this.props.location.state.favorites !== null &&
            this.props.location.state.favorites.map(recipe => {
              return (
                <div
                  key={recipe.recipe_id}
                  className="col-md-4"
                  style={{ marginBottom: "2rem" }}
                >
                  <div className="recipes__box">
                    <LazyLoad>
                      <div className="image">
                        <img
                          className="recipes__box-img"
                          src={recipe.image_url}
                          alt={recipe.title}
                        />
                      </div>
                    </LazyLoad>
                    <div className="recipe__text">
                      <h5 className="recipes__title">
                        {recipe.title.length < 20
                          ? `${recipe.title}`
                          : `${recipe.title.substring(0, 25)}...`}
                      </h5>
                      <p className="recipes__subtitle">
                        Publisher: <span>{recipe.publisher}</span>
                      </p>
                    </div>
                    <button className="recipe_buttons">
                      <Link
                        to={{
                          pathname: `/recipe/${recipe.recipe_id}`,
                          state: { recipe: recipe.title }
                        }}
                      >
                        View Recipe
                      </Link>
                    </button>
                    {<div className="heart">
                      <span onClick={() => this.props.location.state.setFavorite(recipe)}><Favorite displayHeart={this.displayHeart()}/></span>
                    </div>
                      }
                  </div>
                </div>
              );
            })}
        </div>
      </div>
    );
  }
}

export default Favorites;
